.black-nav{
  display: flex;
  background-color: #333;
  width: 100%;
  color: white;
  padding-left: 20px;
}
div{
  box-sizing: border-box;
}
.list{
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid gray;
}

#btn1{
  text-align: center;
  background-color: black;
